import ReactDOM from "react-dom";
import React, { Component } from "react";

class Thoughts extends Component {
  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll);
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }

  handleScroll = event => {
    const rect = ReactDOM.findDOMNode(this).getBoundingClientRect();
    const { height, top, bottom } = rect;
    const windowHeight = window.innerHeight;
    const pxVisible = Math.max(
      0,
      top > 0
        ? Math.min(height, windowHeight - top)
        : Math.min(bottom, windowHeight)
    );
    const inSection = pxVisible / windowHeight >= 0.5;
    if (inSection) {
      this.props.changeActive("Thoughts");
    }
  };

  render() {
    const { gray, after, before } = this.props;
    return (
      <div
        id="thoughts"
        className={`section ${gray ? "section-gray" : ""} ${
          before ? "section-separator-before" : ""
        } ${after ? "section-separator-after" : ""}`}
      >
        <h1 className="section-title">Thoughts</h1>
        <hr className="section-title-underline" />
        <div className="section-body container">
          <a className="card sharp-corner post mt-3 mb-3" href="!#">
            <div className="card-body">
              <div className="row">
                <div className="col-7 post-name">My first post</div>
                <div className="col-5 post-date text-right">18-02-1996</div>
              </div>
            </div>
          </a>
          <a className="card sharp-corner post mt-3 mb-3" href="!#">
            <div className="card-body">
              <div className="row">
                <div className="col-7 post-name">My second post</div>
                <div className="col-5 post-date text-right">18-02-1996</div>
              </div>
            </div>
          </a>
          <a className="card sharp-corner post mt-3 mb-3" href="!#">
            <div className="card-body">
              <div className="row">
                <div className="col-7 post-name">My third post</div>
                <div className="col-5 post-date text-right">18-02-1996</div>
              </div>
            </div>
          </a>
        </div>
      </div>
    );
  }
}

export default Thoughts;
